.review-list .container{
    text-align: center;
    background: #eee;
    display: grid;
    grid-row-gap: 1em;
    }
.review-list  .container > div:nth-child(odd) {
    background: #ddd;
    }
.details{
    display: grid;
    grid-template-columns: repeat(14, 1fr);
    }
.review-list .headers {
    display: grid;
    grid-template-columns: repeat(14, 1fr);
    text-align: center;
    }
